import { Component, OnInit, OnDestroy } from '@angular/core';
import { ProductSlider } from '../../shared/data/slider';
import { Product } from '../../shared/classes/product';
import { ProductService } from '../../shared/services/product.service';
import { ProductoService } from 'src/app/shared/services/productos/producto.service';
import { environment } from 'src/environments/environment';
import { title } from 'node:process';

@Component({
  selector: 'app-home-lumar',
  templateUrl: './home-lumar.component.html',
  styleUrls: ['./home-lumar.component.scss']
})
export class HomeLumarComponent implements OnInit, OnDestroy {

  urlApi = environment.apiBaseUrl

  public products: Product[] = [];
  public product_list: any[] = [];
  public product_list_especial: any[] = [];
  public productCollections: any[] = [];

  active

  currentYear: number

  listaVacia: any = undefined;

  //tamaño de paginacion e inicio
  public page = 1
  public pageSize = 10
  public layoutView: string = 'grid-view';
  public grid: string = 'col-xl-2 col-md-6';

  // Collection banner
  public collections = [{
    image: 'assets/images/collection/lumar/hombre.jpg',
    save: 'Grandes ofertas en ropa para',
    title: 'Hombre',
    ruta: '/shop/categoria/ropa-hombre'
  }, {
    image: 'assets/images/collection/lumar/mujer.jpg',
    save: 'Grandes ofertas en ropa para',
    title: 'Mujer',
    ruta: '/shop/categoria/ropa-mujer'
  }];


  //Blog

  // Blog
  public blog = [{
    image: 'assets/images/blog/regalos.jpg',
    title: 'Regalos',
    ruta: '/shop/categoria/regalos'
  }, {
    image: 'assets/images/blog/decoraciones.png',
    title: 'Decoraciones',
    ruta: '/shop/categoria/decorativas'
  }, {
    image: 'assets/images/blog/hogar.png',
    title: 'Hogar',
    ruta: '/shop/categoria/hogar'
  }, {
    image: 'assets/images/blog/oficina.png',
    title: 'Oficina',
    ruta: '/shop/categoria/oficina'
  }];



  constructor(
    public productService: ProductService,
    public productoService: ProductoService
  ) {
  }

  public ProductSliderConfig: any = ProductSlider;

  public sliders = [{
    title: '',
    subTitle: 'Decoraciones',
    image: 'assets/images/slider/colibri.jpg',
    url: '/shop/categoria/decorativas'
  }, {
    title: '',
    subTitle: 'Joyería',
    image: 'assets/images/slider/joyeria.png',
    url: '/shop/categoria/joyeria'
  }];

  ngOnInit(): void {
    document.body.classList.add('box-layout-body');
    this.listarDiezProductos();
    this.listarProductosEspeciales();
  }

  ngOnDestroy() {
    document.body.classList.remove('box-layout-body');
  }

  // Product Tab collection
  getCollectionProducts(collection) {
    return this.products.filter((item) => {
      if (item.collection.find(i => i === collection)) {
        return item
      }
    })
  }

  //Obtener los ultimos diez productos
  listarDiezProductos() {
    this.productoService.getAllTenProductos().subscribe(
      (data: any) => {
        this.product_list = data.map((product: any) => {

          return {
            ...product,
            img_url: product.imagen_producto && product.imagen_producto.length > 0 ? product.imagen_producto[0].imagen_url : 'path/to/default/image.jpg'
          };
        });
      },
      error => {
        console.error('Error al obtener productos', error);
      }
    );
  }

  //Obtener los productos especiales
  listarProductosEspeciales() {
    this.productoService.getAllEspecialesProductos().subscribe(
      (data: any) => {
        this.product_list_especial = data.map((product: any) => {

          return {
            ...product,
            img_url: product.imagen_producto && product.imagen_producto.length > 0 ? product.imagen_producto[0].imagen_url : 'path/to/default/image.jpg'
          };
        });
      },
      error => {
        console.error('Error al obtener productos', error);
      }
    );
  }


}
